import '../scss/style.scss';


var variableCookie = getCookie("migas");

var finalUrl = window.location.href.substring(window.location.href.indexOf("/", "http://".length)+1);


if (variableCookie == "" || finalUrl == ""){
	document.cookie = "migas=index.html|Portada,";
} else if (!variableCookie.includes(finalUrl)){
	var auxCookie = "migas=" + variableCookie + finalUrl + "|" + document.querySelector("h1").innerHTML + ",";
	document.cookie = auxCookie;
} else{
	variableCookie = "migas=" + variableCookie.substring(0, document.cookie.indexOf(finalUrl));
	if (finalUrl=="index.html"){
		variableCookie = variableCookie + finalUrl + "|Portada,";
	} else {
		variableCookie = variableCookie + finalUrl + "|" + document.querySelector("h1").innerHTML + ",";
	}
	document.cookie = variableCookie;
}

if (document.getElementById("migas")!= null && document.getElementById("migas").innerHTML.length == 0){
	variableCookie = getCookie("migas");
	var aux = variableCookie.substring(0, variableCookie.length-1);
	var imprimirMiga = "";
	var arrayComas = aux.split(",");
	var arrayAux;
	for (var i = 0; i < arrayComas.length; i++){
		if (i==arrayComas.length-1){
			arrayAux = arrayComas[i].split("|");
			imprimirMiga += '<li class="breadcrumb-item active">' + arrayAux[1] + '</li>';
		} else {
			arrayAux = arrayComas[i].split("|");
			imprimirMiga += '<li class="breadcrumb-item"><a href="' + arrayAux[0] + '">' + arrayAux[1] + '</a></li>';
		}
	}
	document.getElementById("migas").innerHTML=imprimirMiga;
}

function getCookie(cname) {
  var name = cname + "=";
  var decodedCookie = decodeURIComponent(document.cookie);
  var ca = decodedCookie.split(';');
  for(var i = 0; i <ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) == 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

